import { useEffect } from "react";
import { useAppDispatch } from "../../../app/hooks";
import { Timing } from "../../../constants";
import { startNewRound } from "../playerSlice";

export const PlayerStarted = () => {
  const d = useAppDispatch();
  useEffect(() => {
    setTimeout(() => {
      d(startNewRound());
    }, Timing.GameStarted);
  });

  return (
    <div className='fullscreen centered'>
      <div className='instructions'>Check on your phone to know your team</div>
      <div className='versus'>
        <div className='versus__team'>Blue team</div><div className='versus__versus'>vs</div><div className='versus__team'>Red team</div>
      </div>
    </div>
  );
};
