export enum PlayerStatus {
  Loading,
  WaitingForPlayers,
  Started,
  RoundStarted,
  RolesShown,
  GuessingStarted,
  TeamResultsShown,
  Ended,
  Error,
}

export enum ControllerStatus {
  WaitingForPin,
  WaitingForName,
  WaitingForGameToStart,
  GameStarted,
  RoundStarted,
  RolesShown,
  GuessingStarted,
  TeamResultsShown,
  Error,
}

export interface TeamAssignment {
  cid: string;
  team: number;
  instructorOrder: number;
}

export namespace Team {
  export const Blue = 0;
  export const Red = 1;
}

export namespace Timing {
  export const GameStarted = 1000;
  export const RoundStarted = 1000;
  export const RolesShown = 1000;
  export const GuessingSession = 30000;
  export const TeamResultsShown = 1000;
}

export interface Round {
  id: number;
  scores: number[];
}

export interface Turn {
  team: number;
  instructor?: User;
  guessedWords: string[];
}

export interface User {
  cid: string;
  name: string;
}

export interface Guess {
    correctAnswers: number;
    correctWord: string;
    incorrectWords: string[];
}