import clsx from "clsx";
import { useAppSelector } from "../../../app/hooks";
import { Team } from "../../../constants";
import { selectControllerClientId, selectControllerTeamAssignments } from "../controllerSlice";

export const ControllerGameStarted = () => {
  const clientId = useAppSelector(selectControllerClientId);
  const teamAssignments = useAppSelector(selectControllerTeamAssignments);
  const team = teamAssignments?.find(x => x.cid === clientId)?.team;
  return (
    <div className={clsx('fullscreen centered', team === Team.Red ? 'red' : 'blue')}>
      You are in the {team === Team.Red ? 'RED' : 'BLUE'} team!
    </div>
  );
};
