import { useEffect } from "react";
import { useAppDispatch, useAppSelector } from "../../../app/hooks";
import { Team, Timing } from "../../../constants";
import { selectPlayerRound, selectPlayerTurn, startGuessing } from "../playerSlice";

export const PlayerRolesShown = () => {
  const round = useAppSelector(selectPlayerRound);
  const turn = useAppSelector(selectPlayerTurn);
  const d = useAppDispatch();
  useEffect(() => {
    setTimeout(() => {
      d(startGuessing());
    }, Timing.RolesShown);
  });
  
  return (
    <div className='fullscreen centered'>
      <h1>Round {round?.id} - Team {turn?.team === Team.Blue ? 'BLUE' : 'RED' }</h1>
      <div className='instructions'>Guess as many words as you can. The instructor CANNOT use words of the same family.</div>
    </div>
  );
};
