import { createAsyncThunk, createSlice, PayloadAction } from "@reduxjs/toolkit";
import { RootState } from "../../app/store";
import { getVotingProgress, shuffle } from "../../app/utils";
import {
  Guess,
  PlayerStatus,
  Round,
  TeamAssignment,
  Turn,
  User,
} from "../../constants";
import { createGameSession } from "./playerAPI";

export interface PlayerState {
  pin?: string;
  round?: Round;
  turn?: Turn;
  users: User[];
  status: PlayerStatus;
  words?: string[];
  errorMessage: string | null;
  teamAssignments?: TeamAssignment[];
  guess?: Guess;
}

const initialState: PlayerState = {
  users: [],
  status: PlayerStatus.Loading,
  errorMessage: null,
};

let roundCounter = 0;
let turnCounter = 0;

export const initializeAsync = createAsyncThunk(
  "player/initialize",
  async () => {
    const response = await createGameSession();
    return response;
  }
);

let wordIndex = 0;
const alreadyGuessedWords: string[] = [];

function findRandomWordsNotAlreadyGuessed(
  allWords: string[],
  wordToIgnore: string,
  count: number
) {
  const allValidGuesses = allWords.filter(
    (x) => !alreadyGuessedWords.includes(x) && x !== wordToIgnore
  );
  const guesses = shuffle(allValidGuesses).splice(0, count);
  // TODO: not enough items? Game over.
  return guesses;
}

function updateRound(turnCounter: number, state: PlayerState) {
  const team = turnCounter % 2;
  const teamUsers = state.teamAssignments?.filter((x) => x.team === team) ?? [];
  // TODO: here we assume the players are already in instructorOrder but really we should explicitly sort
  const instructor = teamUsers[(turnCounter - 1) % teamUsers.length];
  state.round = { id: roundCounter, scores: [0, 0] };
  state.turn = {
    guessedWords: [],
    team,
    instructor: state.users.find((x) => x.cid === instructor.cid),
  };
}

export const playerSlice = createSlice({
  name: "player",
  initialState,
  reducers: {
    showTeamResults: (state) => {
      state.status = PlayerStatus.TeamResultsShown;
    },
    goToNextTeam: (state) => {
      turnCounter++;
      state.status = PlayerStatus.RoundStarted;
      updateRound(turnCounter, state);
    },
    onVoteSubmitted: (state, action: PayloadAction<string>) => {
      if (action.payload === state.guess?.correctWord) {
        state.guess.correctAnswers++;

        const progress =
          state.round && state.guess
            ? getVotingProgress(
                state.teamAssignments ?? [],
                state.turn,
                state.guess
              )
            : 0;
        // guessed correctly
        if (progress >= 100) {
          state.words?.shift();
          const allWords = state.words ?? [];
          alreadyGuessedWords.push(state.guess.correctWord);
          if (state.round && state.turn) {
            state.turn.guessedWords.push(state.guess.correctWord);
            state.round.scores[state.turn.team]++;
          }
          state.guess = {
            correctAnswers: 0,
            correctWord: allWords[wordIndex],
            incorrectWords: findRandomWordsNotAlreadyGuessed(
              allWords,
              allWords[wordIndex],
              5
            ),
          };
        }
      }
    },
    onSkipCurrentGuess: (state) => {
      // push word to the back
      // start a new guess
      if (state.words?.length) {
        const words = state.words ?? [];
        const firstWord = words.shift();
        if (firstWord) {
          state.words = [...words, firstWord];
        }
      }
    },
    startGuessing: (state) => {
      state.status = PlayerStatus.GuessingStarted;

      const allWords = state.words ?? [];
      state.guess = {
        correctAnswers: 0,
        correctWord: allWords[wordIndex],
        incorrectWords: findRandomWordsNotAlreadyGuessed(
          allWords,
          allWords[wordIndex],
          5
        ),
      };
    },
    showRoles: (state) => {
      state.status = PlayerStatus.RolesShown;
    },
    startNewRound: (state) => {
      state.status = PlayerStatus.RoundStarted;
      roundCounter++;
      turnCounter++;
      updateRound(turnCounter, state);
    },
    userJoined: (state, action: PayloadAction<User>) => {
      state.users.push(action.payload);
    },
    setIsLoaded: (state) => {
      state.status = PlayerStatus.WaitingForPlayers;
    },
    setPlayerError: (state, action: PayloadAction<string>) => {
      state.status = PlayerStatus.Error;
      state.errorMessage = action.payload;
    },
    startGame: (
      state,
      action: PayloadAction<{
        words?: string[];
        teamAssignments?: TeamAssignment[];
      }>
    ) => {
      state.status = PlayerStatus.Started;
      state.words = action.payload.words;
      state.teamAssignments = action.payload.teamAssignments;
    },
    sendMessageFromPlayer: (state, action: PayloadAction<string>) => {
      // handled in middleware
    },
  },
  // The `extraReducers` field lets the slice handle actions defined elsewhere,
  // including actions generated by createAsyncThunk or in other slices.
  extraReducers: (builder) => {
    builder
      .addCase(initializeAsync.fulfilled, (state, action) => {
        state.pin = action.payload.pin;
      })
      .addCase(initializeAsync.rejected, (state) => {
        state.status = PlayerStatus.Error;
        state.errorMessage = "initializeAsync.rejected";
      });
  },
});

export const selectPlayerStatus = (state: RootState) => state.player.status;
export const selectPlayerPin = (state: RootState) => state.player.pin;
export const selectPlayerGuess = (state: RootState) => state.player.guess;
export const selectPlayerRound = (state: RootState) => state.player.round;
export const selectPlayerTurn = (state: RootState) => state.player.turn;
export const selectPlayerUsers = (state: RootState) => state.player.users;
export const selectPlayerTeamAssignments = (state: RootState) =>
  state.player.teamAssignments;
export const selectPlayerErrorMessage = (state: RootState) =>
  state.player.errorMessage;

export const {
  showTeamResults,
  goToNextTeam,
  onVoteSubmitted,
  onSkipCurrentGuess,
  setIsLoaded,
  setPlayerError,
  startGame,
  sendMessageFromPlayer,
  userJoined,
  startNewRound,
  showRoles,
  startGuessing,
} = playerSlice.actions;

export default playerSlice.reducer;
