import clsx from "clsx";
import { useEffect } from "react";
import { useAppDispatch, useAppSelector } from "../../../app/hooks";
import { Team, Timing } from "../../../constants";
import { selectPlayerTurn, showRoles } from "../playerSlice";

export const PlayerRoundStarted = () => {
  const turn = useAppSelector(selectPlayerTurn);

  const d = useAppDispatch();
  useEffect(() => {
    setTimeout(() => {
      d(showRoles());
    }, Timing.RoundStarted);
  });
  
  return (
    <div className='fullscreen centered'>
      <div className='instructions'>Get ready {turn?.team === Team.Blue ? 'BLUE' : 'RED' } team!</div>
      <div className='versus'>
        <div className={clsx('versus__team',  { selected: turn?.team === Team.Blue })}>Blue team</div><div className='versus__versus'>vs</div><div className={clsx('versus__team',  { selected: turn?.team === Team.Red })}>Red team</div>
      </div>
    </div>
  );
};
