import { useEffect, useRef, useState } from "react";
import styles from './Countdown.module.css';

export const Countdown = ({ duration, onExpiry }: { duration: number; onExpiry: () => void }) => {
    const timeLeftRef = useRef(duration);
    const [, setForceRender] = useState(Date.now());
    useEffect(() => {
        let timeout: NodeJS.Timeout;
        const countDown = () => {
            const newTimeLeft = Math.max(0, timeLeftRef.current - 1000);
            timeLeftRef.current = newTimeLeft;
            setForceRender(Date.now());
            if (newTimeLeft !== 0) {
                timeout = setTimeout(countDown, 1000);
            } else {
                onExpiry();
            }
        };
        timeout = setTimeout(countDown, 1000);
        return () => {
            clearTimeout(timeout);
        }
    }, [onExpiry]);
    return <div className={styles.countdown}>{Math.ceil(timeLeftRef.current / 1000)}</div>;
}