import clsx from "clsx";
import { Team, Turn } from "../../../constants";

export const Versus = ({ turn, scores }: { turn?: Turn | undefined; scores?: number[] }) => {
  return (
    <div className="versus">
      <div
        className={clsx("versus__team", { selected: turn?.team === Team.Blue })}
      >
        <div>Blue team</div>
        {!!scores && <div>{scores[0]}</div>}
      </div>
      <div className="versus__versus">vs</div>
      <div
        className={clsx("versus__team", { selected: turn?.team === Team.Red })}
      >
        <div>Red team</div>
        {!!scores && <div>{scores[1]}</div>}
      </div>
    </div>
  );
};
