import { Guess, TeamAssignment, Turn } from "../constants";

export function shuffle(array: string[]) {
  let currentIndex = array.length,
    randomIndex;

  // While there remain elements to shuffle.
  while (currentIndex !== 0) {
    // Pick a remaining element.
    randomIndex = Math.floor(Math.random() * currentIndex);
    currentIndex--;

    // And swap it with the current element.
    [array[currentIndex], array[randomIndex]] = [
      array[randomIndex],
      array[currentIndex],
    ];
  }

  return array;
}

export function getVotingProgress(teamAssignments: TeamAssignment[], turn: Turn | undefined, guess: Guess) {
  const totalUsersInCurrentTeam = teamAssignments ? teamAssignments.filter(x => x.team === turn?.team).length - 1 /* removes instructor */ : 0;
  const totalVotes = guess?.correctAnswers ?? 0;
  return Math.min(100, Math.round(totalVotes * 100 / totalUsersInCurrentTeam));
}