import { useEffect } from "react";
import { useAppDispatch, useAppSelector } from "../../../app/hooks";
import { Team, Timing } from "../../../constants";
import {
  goToNextTeam,
  selectPlayerRound,
  selectPlayerTurn,
} from "../playerSlice";
import { Versus } from "./Versus";

export const PlayerTeamResultsShown = () => {
  const round = useAppSelector(selectPlayerRound);
  const turn = useAppSelector(selectPlayerTurn);

  const d = useAppDispatch();
  useEffect(() => {
    setTimeout(() => {
      d(goToNextTeam());
    }, Timing.TeamResultsShown);
  });

  return (
    <div className="fullscreen centered">
      <h1>Round {round?.id}</h1>
      <div className="instructions">
        The {turn?.team === Team.Blue ? "BLUE" : "RED"} team guessed
      </div>
      <div className="card-list">
        {turn?.guessedWords.map((x) => (
          <div key={x} className="card">
            {x}
          </div>
        ))}
      </div>
      <Versus scores={round?.scores} />
    </div>
  );
};
