import clsx from "clsx";
import { useEffect, useMemo, useState } from "react";
import { useAppDispatch, useAppSelector } from "../../../app/hooks";
import { shuffle } from "../../../app/utils";
import {
  selectControllerClientId,
  selectControllerGuess,
  selectControllerTeamAssignments,
  selectControllerTurn,
  skipCurrentGuess,
  submitVote,
} from "../controllerSlice";

const VoteButton = ({
  color,
  disabled,
  label,
  selected,
  onClick,
}: {
  color: string;
  disabled: boolean;
  label: string;
  selected: boolean;
  onClick: (value: string) => void;
}) => {
  return (
    <button
      disabled={disabled}
      className={clsx("button--answer", color, { selected })}
      onClick={() => onClick(label)}
    >
      {label}
    </button>
  );
};

export const ControllerGuessingStarted = () => {
  const [userVote, setUserVote] = useState("");
  const turn = useAppSelector(selectControllerTurn);
  const clientId = useAppSelector(selectControllerClientId);
  const myTeam = useAppSelector(selectControllerTeamAssignments)?.find(
    (x) => x.cid === clientId
  )?.team;
  const guess = useAppSelector(selectControllerGuess);
  const words = useMemo(() => {
    if (!guess) {
      return [];
    }
    return shuffle([...guess?.incorrectWords, guess?.correctWord]);
  }, [guess]);

  useEffect(() => {
    setUserVote("");
  }, [guess]);

  const d = useAppDispatch();
  const onVote = (word: string) => {
    setUserVote(word);
    d(submitVote(word));
  };
  return (
    <div className="fullscreen centered">
      {clientId === turn?.instructor?.cid && (
        <>
          <div className="centered" style={{ flex: 1 }}>
          <div className="instructions">Make your team guess</div>
            <h1>{guess?.correctWord}</h1>
          </div>
          <div>
            <button className="button--big" onClick={() => d(skipCurrentGuess())}>Pass</button>
          </div>
        </>
      )}
      {clientId !== turn?.instructor?.cid && turn?.team === myTeam && (
        <>
          <div className="centered" style={{ flex: 1 }}>
            <h1>Choose the right answer</h1>
          </div>
          <div className="answer-list">
            <VoteButton
              color="red"
              label={words[0]}
              onClick={onVote}
              selected={userVote === words[0]}
              disabled={!!userVote}
            />
            <VoteButton
              color="blue"
              label={words[1]}
              onClick={onVote}
              selected={userVote === words[1]}
              disabled={!!userVote}
            />
            <VoteButton
              color="orange"
              label={words[2]}
              onClick={onVote}
              selected={userVote === words[2]}
              disabled={!!userVote}
            />
            <VoteButton
              color="green"
              label={words[3]}
              onClick={onVote}
              selected={userVote === words[3]}
              disabled={!!userVote}
            />
            <VoteButton
              color="pink"
              label={words[4]}
              onClick={onVote}
              selected={userVote === words[4]}
              disabled={!!userVote}
            />
            <VoteButton
              color="violet"
              label={words[5]}
              onClick={onVote}
              selected={userVote === words[5]}
              disabled={!!userVote}
            />
          </div>
        </>
      )}
      {turn?.team !== myTeam && <div>The other team is playing</div>}
    </div>
  );
};
