import { useState } from "react";
import { useAppDispatch, useAppSelector } from "../../../app/hooks";
import {
  selectPlayerPin,
  selectPlayerUsers,
  startGame,
} from "../playerSlice";

export const PlayerWaitingForPlayers = () => {
  const d = useAppDispatch();
  const pin = useAppSelector(selectPlayerPin);
  const users = useAppSelector(selectPlayerUsers);
  const [isStarting, setIsStarting] = useState(false);
  const doStartGame = () => {
    setIsStarting(true);
    d(startGame({}));
  };

  return (
    <div>
      <h1>PIN: {pin}</h1>
      Waiting for players... ({users.length} joined)

      <div className='card-list'>
        {users.map(x => <div className='card' key={x.cid}>{x.name}</div>)}
      </div>
      <div>
        <button disabled={isStarting} onClick={doStartGame}>
          Start
        </button>
      </div>
    </div>
  );
};
