import { configureStore, ThunkAction, Action } from '@reduxjs/toolkit';
import playerReducer from '../features/player/playerSlice';
import controllerReducer from '../features/controller/controllerSlice';
import { playerSocketMiddleware } from '../features/player/playerSocketMiddleware';
import { controllerSocketMiddleware } from '../features/controller/controllerSocketMiddleware';

export const store = configureStore({
  reducer: {
    player: playerReducer,
    controller: controllerReducer,
  },
  middleware: (getDefaultMiddleware) => {
    return getDefaultMiddleware().concat(playerSocketMiddleware).concat(controllerSocketMiddleware)
  }
});

export type AppDispatch = typeof store.dispatch;
export type RootState = ReturnType<typeof store.getState>;
export type AppThunk<ReturnType = void> = ThunkAction<
  ReturnType,
  RootState,
  unknown,
  Action<string>
>;
