export function createGameSession() {
  return fetch("https://game.kahoot-stage.it/reserve/session/", {
    method: "POST",
    mode: "cors",
    cache: "no-cache",
    credentials: "same-origin",
    headers: {
      "Content-Type": "application/json",
    },
    redirect: "follow",
    referrerPolicy: "no-referrer",
    body: JSON.stringify({
      gameType: "normal",
      twoFactorAuth: false,
      namerator: false,
      participantId: false,
      smartPractice: false,
      gameMode: "normal",
      teamMode: false,
      collaborations: false,
    }),
  }).then(async (response) => {
    const pin = await response.text();
    return {
      pin,
      sessionToken: response.headers.get("x-kahoot-session-token"),
    };
  });
}
