import { useAppDispatch, useAppSelector } from "../../../app/hooks";
import { Countdown } from "../../../components/Countdown";
import { VoteBar } from "./VoteBar";
import { Team, Timing } from "../../../constants";
import { selectPlayerRound, selectPlayerTurn, showTeamResults } from "../playerSlice";
import { useCallback } from "react";

export const PlayerGuessingStarted = () => {
  const round = useAppSelector(selectPlayerRound);
  const turn = useAppSelector(selectPlayerTurn);
  
  const d = useAppDispatch();
  const onExpiry = useCallback(() => {
    d(showTeamResults());
  }, [d]);
  return (
    <div className='fullscreen centered'>
      <h1>Round {round?.id} - Team {turn?.team === Team.Blue ? 'BLUE' : 'RED' }</h1>
      <Countdown duration={Timing.GuessingSession} onExpiry={onExpiry} />
      <VoteBar />
    </div>
  );
};
