import { CometD } from "cometd";
import { Middleware } from "redux";
import {
  sendMessageFromController,
  setClientId,
  setControllerError,
  setName,
  skipCurrentGuess,
  submitVote,
} from "./controllerSlice";
import { RootState } from "../../app/store";
import { AnyAction, Dispatch, MiddlewareAPI } from "@reduxjs/toolkit";
import { onSkipCurrentGuess, onVoteSubmitted } from "../player/playerSlice";

const cometd = new CometD();

function sendMessageToPlayer(
  store: MiddlewareAPI<Dispatch<AnyAction>, any>,
  message: any
) {
  const state = store.getState() as RootState;
  const pin = state.controller.pin;
  cometd.publish("/service/controller", {
    id: 0,
    type: "message",
    gameid: pin,
    host: "kahoot-stage.it",
    content: JSON.stringify(message),
  });
}

export const controllerSocketMiddleware: Middleware =
  (store) => (next) => (action) => {
    if (submitVote.match(action)) {
      sendMessageToPlayer(store, onVoteSubmitted(action.payload));
      return next(action);
    }
    if (skipCurrentGuess.match(action)) {
      sendMessageToPlayer(store, onSkipCurrentGuess());
      return next(action);
    }
    if (sendMessageFromController.match(action)) {
      sendMessageToPlayer(store, action.payload);
      return next(action);
    }

    if (setName.match(action)) {
      const state = store.getState() as RootState;
      const pin = state.controller.pin;
      const sessionId = state.controller.sessionId;
      const url = `https://kahoot-stage.it/cometd/${pin}/${sessionId}`;

      cometd.configure({
        url,
        logLevel: "debug",
        maxNetworkDelay: 40_000,
      });

      cometd.handshake((response) => {
        if (response.successful) {
          cometd.publish("/service/controller", {
            type: "login",
            gameid: pin,
            host: "kahoot-stage.it",
            name: action.payload,
            content: JSON.stringify({
              nickname: action.payload,
              content: {
                device: {
                  screen: {},
                  userAgent: "123",
                },
              },
            }),
            participantUserId: "",
          });
        } else {
          store.dispatch(setControllerError("Cannot connect to cometd"));
        }
      });

      cometd.subscribe("/service/controller", (message) => {
        if (message.data.type === "loginResponse") {
          store.dispatch(setClientId(message.data.cid));
          return;
        }
      });

      cometd.subscribe("/service/player", (message) => {
        store.dispatch(JSON.parse(message.data.content));
      });
      return next(action);
    }

    next(action);
  };
