import { useAppSelector } from "../../../app/hooks";
import {
  selectControllerClientId,
  selectControllerTeamAssignments,
  selectControllerTurn,
} from "../controllerSlice";

export const ControllerRolesShown = () => {
  const turn = useAppSelector(selectControllerTurn);
  const clientId = useAppSelector(selectControllerClientId);
  const myTeam = useAppSelector(selectControllerTeamAssignments)?.find(x => x.cid === clientId)?.team;
  return (
    <div className="fullscreen centered">
      {clientId === turn?.instructor?.cid && (
        <div>
          <h1>You are the instructor!</h1>
          <div className="instructions">
            Make your team guess as many words as possible!
          </div>
        </div>
      )}
      {clientId !== turn?.instructor?.cid && turn?.team === myTeam && (
        <div>
          Listen to {turn?.instructor?.name} and try to guess as many words as
          possible!
        </div>
      )}
      {turn?.team !== myTeam && (
        <div>
          The other team is playing
        </div>
      )}
    </div>
  );
};
